.nav {
  display: flex;
  max-width: 100%;
  height: 60px;
  color: #fff;
  justify-content: center;
}

.channel {
  flex: 0 0 33.333333%;
  text-align: center;
  font-size: 1.5rem;
  line-height: 3.5rem;
  opacity: 0.5;
  background-color:black;
  cursor: pointer;
  transition: 0.3s;
}
.channel:hover{
   opacity: 0.9;
   background-color:#000000
}


.header {
  font-size: 3.5rem;
  color: #fff;
  font-weight: 350;
  text-align: center;
  margin: auto;
}
.description {
  font-size: 1rem;
  color: #fff;
  font-weight: 200;
  text-align: center;
  margin: 10px auto;
}
.streamsContainer {
  max-width: 1140px;
  display: flex;
  flex-wrap: wrap;
  margin: auto;
}

.streams {
  color: #fff;
  max-width: 25%;
  flex: 0 0 25%;
  opacity: 0.8;
  box-shadow: 4px 4px 5px rgba(25, 25, 25, 1);
  transition: 0.3s;
  text-decoration: none;
  padding: 4px;
  box-sizing: border-box;
  background-color: black;
}

@media (max-width: 992px) {
  .streams {
    flex: 0 0 33.333333%;
    max-width: 33.333333%;
  }
}

@media (max-width: 768px) {
  .streams {
    flex: 0 0 50%;
    max-width: 50%;
  }
}

@media (max-width: 576px) {
  .streams {
    flex: 0 0 100%;
    max-width: 100%;
  }
}

.streamsImg {
  width: 100%;
  height: 65%;
  vertical-align: top;
}

.streams:hover {
  transform: scale(1.05);
  z-index: 1;
  box-shadow: 0 0 2rem black;
}

.profile {
  display: flex;
  background-color: black;
  height: 35%;
}

.profileImgContainer {
  max-width: 25%;
  flex: 0 0 25%;
  padding: 0.5rem;
  box-sizing: border-box;
}

.profileImg {
  border-radius: 50%;
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

.detail {
  display: flex;
  max-width: 75%;
  flex-direction: column;
  flex-wrap: wrap;
  flex: 0 0 75%;
}

.title {
  margin-left: 5px;
  width: 100%;
  height: 50%;
  overflow: hidden;
  font-size: 1rem;
  text-overflow: ellipsis;
  white-space: nowrap;
  box-sizing: border-box;
  line-height: 2rem;
}

.name {
  margin-left: 5px;
  width: 100%;
  height: 40%;
  box-sizing: border-box;
}
